@use "./variables";
@use "./animations";
@use "./mixins";
/* ===================================== 
    -----/socials------
======================================== */
.hamburgerContainer{
    height: 80px;
    width: 80px;
    overflow: hidden;
    .hamburgerMenu {
        color: black;
        display: flex;
        cursor: pointer;
        transition: 0.5s;
        text-align: center;
        display: block;
        width: 70%;
        height: 100%;
        font-size: 3rem;
        line-height: 5rem;   
    }
    .toggle {
        transform: translateY(-100%);
    }
    
}
.hamburgerList {
    @include mixins.flexCentering(column, center);
    justify-content: center;
    position: fixed;
    top: 5.1rem;
    background-color: white;
    width: 80%;
    height:40%;
    z-index: 1;
    li {
        font-size: 1.5rem;
    }
}
